html{box-sizing:border-box}*,*::before,*::after{box-sizing:inherit}body{font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;width:100%;height:100%;background-color:rgba(226,224,224,0.171)}body,div,header,main,footer,section,article,h1,h2,h3,p,a,ul,ol,li,address,img,form,input,aside,span{padding:0;margin:0;list-style:none;text-decoration:none;font-size:100%;vertical-align:baseline;outline:none}a,img,main,input,::after,::before{display:block}.link_item{display:flex;flex-direction:column;justify-content:center;align-items:center;width:100%;font-size:1.3em;color:#18195f}

